@tailwind base;
@tailwind components;
@tailwind utilities;


.content{
    height: 50%;
    overflow-x: scroll;
    
  }
  
  .popup-content {
    margin: auto;
    background: rgb(255, 255, 255);
    width: 50%;
    padding: 5px;
  }
  
  [role='tooltip'].popup-content {
    width: 200px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
  }
  
  .popup-overlay {
    background: rgba(0, 0, 0, 0.5);
  }
  
  
  
  [data-popup='tooltip'].popup-overlay {
    background: transparent;
  }